<template>
  <div id="order-history">
    <order-nav></order-nav>
    <h3>历史工单</h3>
    <div class="order_query">
      <div class="left">
        <span>结单时间：</span>
        <el-date-picker
          v-model="checkedDate"
          type="date"
          placeholder="选择日期"
          format="yyyy/MM/dd"
          value-format="yyyy/MM/dd"
        ></el-date-picker>
      </div>
      <div class="right">
        <el-button @click="queryData" type="primary" size="small">查询</el-button>
      </div>
    </div>
    <div class="order_content">
      <el-table
        :data="historyList"
        border
        style="width: 90%;margin:0 auto"
        max-height="500"
        size="small"
      >
        <el-table-column type="index" label="序号" width="60"></el-table-column>
        <el-table-column prop="receive_person" label="接单人"></el-table-column>
        <el-table-column prop="initiate_time" label="发单时间"></el-table-column>
        <el-table-column prop="out_time" label="结单时间"></el-table-column>
        <el-table-column prop="need" label="内容需求"></el-table-column>
        <el-table-column prop="next_info" label="跟进信息"></el-table-column>
        <el-table-column prop="job_type" label="工单类型"></el-table-column>
        <el-table-column prop="status" label="状态"></el-table-column>
      </el-table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="50"
        layout="total,prev, pager, next"
        :total="total"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
import orderNav from "@/common/order_nav";
export default {
  name: "",
  props: {},
  components: {
    orderNav
  },
  data() {
    return {
      checkedDate: "",
      historyList: [],
      // 分页相关
      currentPage: 1,
      total: 0,
      // 是否处于查询状态
      isQuery: false
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取历史工单列表
    getHistoryList() {
      let date = "";
      // 根据是否处于查询状态赋值
      this.isQuery === false ? (date = "") : (date = this.checkedDate);
      this.$api.getOrderHistory(date, this.currentPage).then(res => {
        console.log(res);
        this.historyList = res.data.data;
        this.total = res.data.all_num;
      });
    },
    // 条件查询
    queryData() {
      this.isQuery = true;
      this.getHistoryList();
    },
    // 分页
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getHistoryList();
    }
  },
  created() {
    this.getHistoryList();
  },
  mounted() {}
};
</script>
<style>
#order-history .order_query .form_query .el-form-item__label {
  color: #fff;
}
#order-history .el-date-picker {
  width: 256px;
}
</style>
<style scoped>
#order-history {
  height: 92vh;
  background: rgba(255, 255, 255, 0.7);
}
.order_query {
  display: flex;
  justify-content: center;
  padding-top: 10px;
  align-items: center;
  margin-bottom: 12px;
}
.order_query .left {
  margin-right: 24px;
}
.el-pagination {
  margin-top: 24px;
  text-align: center;
}
h3 {
  text-align: center;
}
</style>


